import { Component, OnInit } from '@angular/core';
import { ConsummingService } from '../services/consumming.service';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage implements OnInit {

  valido = false;
  contra = '';

  messages: string[] = [];
  countDownDate = new Date("May 21, 2021 13:00:00").getTime();
  date: string = '';
  datum: Datum = {};
  quote: string = '';
  from: string = 'English';
  to: string = 'Korean';
  text: string = '';
  out: string = '';
  cat_dog: string = '';
  counter: number = 0;
  message: string = '';

  nasa: NasaData = {};

  select_options = ['English', 'Espa√±ol', 'Korean'];

  select_mapping = {
    'English': ['Espa√±ol', 'Korean', 'Doge', 'Emoji'],
    'Espa√±ol': ['English', 'Korean'],
    'Korean': ['Espa√±ol', 'English'],
  }

  methodMapping = {
    'English': {
      'Espa√±ol': 'enes',
      'Korean': 'enko',
      'Doge': 'doge',
      'Emoji': 'emoji',
    },
    'Espa√±ol': {
      'English': 'esen',
      'Korean': 'esko'
    },
    'Korean': {
      'English': 'koen',
      'Espa√±ol': 'koes'
    }
  }

  constructor(private consumming: ConsummingService) { }

  async ngOnInit() {
    this.updateWiki();
    this.updateQuote();
    this.updateNasa();
    this.updateCatDog();

    const x = setInterval(() => {
      const now = new Date().getTime();
      const distance = this.countDownDate - now;
      const days = Math.floor(distance / (1000 * 60 * 60 * 24));
      const hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      const minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
      const seconds = Math.floor((distance % (1000 * 60)) / 1000);
      this.date = days + "d " + hours + "h " + minutes + "m " + seconds + "s ";
      if (distance < 0) {
        clearInterval(x);
      }
    }, 1000);
  }

  async updateWiki() {
    const now: Date = new Date();
    this.datum = await this.consumming.wiki(now.getMonth() + 1, now.getDate()) as Datum;
  }

  async updateQuote() {
    this.quote = await this.consumming.affirmation() as string;
    this.quote = `"${this.quote}"`;
  }

  changeFrom(event: CustomEvent) {
    this.from = event.detail.value;
    this.translate();
  }

  changeTo(event: CustomEvent) {
    this.to = event.detail.value;
    this.translate();
  }

  changeInput(event: CustomEvent) {
    this.text = event.detail.value;
    this.translate();
  }

  async translate() {
    if (this.text.length > 0) {
      if (
        this.from.length > 0 &&
        this.to.length > 0 &&
        this.methodMapping[this.from][this.to]
      ) {
        this.out = await this.consumming.translate(this.text, this.methodMapping[this.from][this.to]) as string;
      } else {
        this.out = '';
      }
    }
  }

  async updateNasa() {
    this.nasa = await this.consumming.nasa() as NasaData;
  }

  async updateCatDog() {
    this.cat_dog = await this.consumming.catDog() as string;
  }

  repeat() {
    this.messages.length = 0;
    for (let i = 0; i < this.counter; i++) {
      this.messages.push(`${i + 1}) ${this.message}`);
    }
    this.message = '';
  }

  async enviar() {
    this.valido = await this.consumming.enviar(this.contra) as boolean;
    this.contra = '';
  }

}

export interface Datum {
  type?;
  event?;
}

export interface NasaData {
  media_type?;
  url?;
  explanation?;
  title?;
}

